import React from "react";
import { useState } from "react";
import LayoutDefault from "components/LayoutDefault";
import Inputmask from "inputmask";

import styles from "./Confirm.module.scss";
import UserStore from "../../stores/UserStore";
import { RouteComponentProps } from "react-router";
import { inject } from "mobx-react";
interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ConfirmPage: React.FC<IProps> = (props) => {
  // POST /auth/confirm-account
  // POST /auth/request-confirmation-code
  // POST /auth/request-change-phone

  // POST /auth/request-confirmation-code-via-sms

  const { state, user } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");

  const [selectedPhone, setSelectedPhone] = useState("");
  const [selectedCode, setSelectedCode] = useState("");
  const [selectedAlert, setSelectedAlert] = useState("" as any);

  const [
    allowRequestNewConfirmationCode,
    setAllowRequestNewConfirmationCode,
  ] = useState(false);
  const [waitIntervalBetweenTries, setWaitIntervalBetweenTries] = useState(
    30 * 1000
  );

  const [selectedSection, setSelectedSection] = useState("enter-code");
  const [allowEnterCode, setAllowEnterCode] = useState(true);

  const [useUpdatePhone, setUseUpdatePhone] = useState(false);
  // allowedTriesToEnterCode

  React.useEffect(() => {
    useUpdatePhone && canChangePhone() && handleUpdatePhone();
  }, [selectedPhone, useUpdatePhone]);

  React.useEffect(() => {
    // const existingSelectedPhone = sessionStorage.getItem('selectedPhone');
    // if(existingSelectedPhone) {
    //   sessionStorage.removeItem('selectedPhone');
    //   setSelectedPhone(existingSelectedPhone);
    // }
    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
  }, [selectedPhone]);

  React.useEffect(() => {
    // sessionStorage
    if (!selectedAlert) {
      const alert = sessionStorage.getItem("alert");
      sessionStorage.removeItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }

    if (!user) {
      UserStore.getProfile().then((res: any) => {
        setSelectedPhone(res.phone.replace("+7", ""));
      });
    }

    let timeoutTimer;
    if (waitIntervalBetweenTries > 0) {
      timeoutTimer = setTimeout(() => {
        setWaitIntervalBetweenTries(waitIntervalBetweenTries - 1000);
      }, 1000);
    } else if (allowRequestNewConfirmationCode === false) {
      clearTimeout(timeoutTimer);
      setAllowRequestNewConfirmationCode(true);
    }
  }, [selectedAlert, waitIntervalBetweenTries]);

  const canConfirm = () => {
    return selectedPhone.trim() && selectedCode.trim();
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleConfirm();
  };

  const handleConfirm = () => {
    if (canConfirm()) {
      setIsError(false);
      setErrMessage("");
      UserStore.confirmAccount(selectedPhone.trim(), selectedCode.trim())
        .then((res) => {
          // redirect
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              sessionStorage.removeItem("alert");

              sessionStorage.setItem(
                "alert",
                JSON.stringify({
                  text: "Account successfully registered!",
                })
              );
              sessionStorage.setItem("useUpdateUser", JSON.stringify(true));
              resolve();
            }, 100);
          }).then(() => {
            props.history.push("/user");
          });
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  const canRequestNewConfirmationCode = () => {
    return (
      waitIntervalBetweenTries === 0 &&
      allowRequestNewConfirmationCode &&
      canChangePhone()
    );
  };

  const handleRequestNewConfirmationCode = () => {
    // requestConfirmationCode

    if (canRequestNewConfirmationCode()) {
      // keep
    } else {
      return;
    }

    setIsError(false);
    setErrMessage("");
    // setSelectedAlert("");
    UserStore.requestConfirmationCode(selectedPhone.trim())
      .then((res) => {
        // redirect
        sessionStorage.removeItem("alert");

        sessionStorage.setItem(
          "alert",
          JSON.stringify({
            text: "New confirmation code successfully sent!",
          })
        );
        const alert = sessionStorage.getItem("alert");
        sessionStorage.removeItem("alert");
        setSelectedAlert(JSON.parse(alert));

        setAllowEnterCode(true);
        // props.history.push("/signin");
      })
      .catch((err) => {
        setIsError(true);
        setErrMessage(
          err.message || "     "
        );
      });
  };

  const canChangePhone = () => {
    return (
      ["+7", selectedPhone]
        .join("")
        .replaceAll("(", "")
        .replaceAll(")", "")
        .replaceAll(" ", "")
        .replaceAll("-", "")
        .replaceAll("_", "").length === 12
    );
  };
  const handleUpdatePhone = () => {
    if (canChangePhone()) {
      // keep
    } else {
      return;
    }

    return new Promise((resolve, reject) => {
      setIsError(false);
      setErrMessage("");
      // setSelectedAlert("");
      UserStore.requestChangePhone(user.id, selectedPhone.trim())
        .then((res) => {
          // redirect
          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Phone changed!",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          resolve();
        })
        .catch((err) => {
          setIsError(true);
          setErrMessage(err.message || "   ");
          reject();
        });
    });
  };

  return (
    <LayoutDefault>
      <div className={styles["signin-page"]}>
        <div className={styles["signin-form"]}>
          <div>
            <h2>Confirm</h2>
            <div
              style={{
                margin: "auto",
                textAlign: "center",
                width: 400,
                marginBottom: 20,
                fontSize: 17,
              }}
            >
              We sent SMS with code into your phone
              <br /> to verify your registration
            </div>
            {selectedAlert && (
              <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
                {selectedAlert.text}
              </div>
            )}
            {isError && (
              <div
                style={{
                  width: 400,
                  margin: "auto",
                  textAlign: "center",
                  color: "red",
                  marginBottom: 10,
                }}
              >
                {errMessage}
              </div>
            )}

            <div>
              {selectedSection === "enter-code" && allowEnterCode && (
                <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
                  <div className={`phone ${styles["phone"]}`}>
                    <label>
                      {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                      <div style={{ overflow: "hidden" }}>
                        <span className={styles["phone-prefix"]}>+7</span>
                        <input
                          type="text"
                          value={selectedPhone}
                          onChange={(event) => {
                            setSelectedPhone(event.currentTarget.value);
                            setUseUpdatePhone(true);
                          }}
                          placeholder="(___) ___-__-__"
                        />
                      </div>
                    </label>
                  </div>

                  <div className={styles["code"]}>
                    <label>
                      {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                      <input
                        type="text"
                        value={selectedCode}
                        onChange={(event) => {
                          setSelectedCode(event.currentTarget.value);
                        }}
                        placeholder="Code"
                      />
                    </label>
                  </div>
                  <button
                    className={styles["confirm-btn"]}
                    onClick={handleConfirm}
                  >
                    Confirm
                  </button>
                </form>
              )}

              <div>
                {!allowRequestNewConfirmationCode &&
                  waitIntervalBetweenTries > 0 &&
                  selectedSection === "enter-code" && (
                    <div
                      style={{
                        width: 400,
                        fontSize: 17,
                        textAlign: "center",
                        margin: "auto",
                      }}
                    >
                      Please wait {waitIntervalBetweenTries / 1000} sec before
                      requesting <br />
                      new confirmation code
                    </div>
                  )}

                {allowRequestNewConfirmationCode &&
                  selectedSection === "enter-code" && (
                    <div>
                      {!allowEnterCode && (
                        <div className={styles["code"]}>
                          <label>
                            {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                            <input
                              type="text"
                              value={selectedPhone}
                              disabled={true}
                              placeholder="Phone"
                            />
                          </label>
                        </div>
                      )}

                      <button
                        className={styles["request-confirmation-code-btn"]}
                        onClick={handleRequestNewConfirmationCode}
                      >
                        Request new confirmation code
                      </button>
                    </div>
                  )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </LayoutDefault>
  );
};

export default inject("UserStore")(ConfirmPage);
