import React from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { useState } from "react";
import Inputmask from "inputmask";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";

import styles from "./ChangePhone.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ChangePhonePage: React.FC<IProps> = (props) => {
  // POST /auth/request-change-phone-with-confirmation
  // POST /auth/request-confirmation-code-for-change-phone
  // POST /auth/confirm-change-phone-with-verification

  const { state, user } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");
  const [selectedAlert, setSelectedAlert] = useState("" as any);

  const [selectedPhone, setSelectedPhone] = useState("");
  const [selectedCode, setSelectedCode] = useState("");

  const [
    allowRequestNewConfirmationCode,
    setAllowRequestNewConfirmationCode,
  ] = useState(false);
  const [waitIntervalBetweenTries, setWaitIntervalBetweenTries] = useState(
    2 * 1000
  );

  const [selectedSection, setSelectedSection] = useState("enter-code");
  const [allowEnterCode, setAllowEnterCode] = useState(true);

  const [useUpdatePhone, setUseUpdatePhone] = useState(false);

  const [phoneUpdated, setPhoneUpdated] = useState(false);

  React.useEffect(() => {
    useUpdatePhone && canChangePhone() && handleUpdatePhone();
  }, [selectedPhone, useUpdatePhone]);

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
    if (!user) {
      UserStore.getProfile().then((res: any) => {
        setSelectedPhone(res.phone && res.phone.replace("+7", "") || '');
      });
    }

    let timeoutTimer;
    if (waitIntervalBetweenTries > 0) {
      timeoutTimer = setTimeout(() => {
        setWaitIntervalBetweenTries(waitIntervalBetweenTries - 1000);
      }, 1000);
    } else if (allowRequestNewConfirmationCode === false) {
      clearTimeout(timeoutTimer);
      setAllowRequestNewConfirmationCode(true);
    }

    if (sessionStorage.getItem("alert")) {
      const alert = sessionStorage.getItem("alert");
      sessionStorage.removeItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
  });

  // if (state === RequestState.ERROR) return <p>Erorr</p>;

  // if (state !== RequestState.SUCCESS) {
  //   return (
  //     <LayoutUser>
  //       <p>Loading...</p>
  //     </LayoutUser>
  //   );
  // }

  const canConfirm = () => {
    return selectedPhone.trim() && canChangePhone();
  };

  const canChangePhone = () => {
    return (
      ["+7", selectedPhone]
        .join("")
        .replaceAll("(", "")
        .replaceAll(")", "")
        .replaceAll(" ", "")
        .replaceAll("-", "")
        .replaceAll("_", "").length === 12
    );
  };
  const handleUpdatePhone = () => {
    if (canChangePhone()) {
      // keep
    } else {
      return;
    }

    return new Promise((resolve, reject) => {
      setIsError(false);
      setErrMessage("");
      // setSelectedAlert("");
      // request-change-phone-with-verification
      UserStore.requestChangePhoneWithConfirmation(
        user.id,
        selectedPhone.trim()
      )
        .then((res) => {
          // redirect
          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Phone changed!",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          setPhoneUpdated(true);

          // props.history.push("/signin");
          resolve();
        })
        .catch((err) => {
          setIsError(true);
          setErrMessage(err.message || "   ");
          reject();
        });
    });
  };

  const canRequestNewConfirmationCode = () => {
    return (
      waitIntervalBetweenTries === 0 &&
      allowRequestNewConfirmationCode &&
      canChangePhone()
    );
  };

  const handleRequestNewConfirmationCode = () => {
    // requestConfirmationCode

    if (canRequestNewConfirmationCode() && phoneUpdated) {
      // keep
    } else {
      return;
    }

    setIsError(false);
    setErrMessage("");
    // setSelectedAlert("");
    UserStore.requestChangePhoneWithConfirmation(user.id, selectedPhone.trim())
      .then((res) => {
        // redirect
        sessionStorage.removeItem("alert");

        sessionStorage.setItem(
          "alert",
          JSON.stringify({
            text: "New confirmation code successfully sent!",
          })
        );
        const alert = sessionStorage.getItem("alert");
        sessionStorage.removeItem("alert");
        setSelectedAlert(JSON.parse(alert));

        setAllowEnterCode(true);
        // props.history.push("/signin");
      })
      .catch((err) => {
        setIsError(true);
        setErrMessage(
          err.message || "     "
        );
      });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    // handleConfirm();
  };

  const handleConfirm = () => {
    if (canConfirm()) {
      setIsError(false);
      setErrMessage("");

      UserStore.confirmChangePhoneWithVerification(
        user.id,
        selectedPhone.trim(),
        selectedCode.trim()
      )
        .then((res) => {
          // redirect
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              sessionStorage.removeItem("alert");

              sessionStorage.setItem(
                "alert",
                JSON.stringify({
                  text: "Phone successfully changed!",
                })
              );
              sessionStorage.setItem("useUpdateUser", JSON.stringify(true));
              resolve();
            }, 100);
          }).then(() => {
            props.history.push("/user");
          });
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  return (
    <LayoutUser>
      <div className={styles["user-page"]}>
        <h2>Change phone</h2>
        <div
          style={{
            margin: "auto",
            textAlign: "center",
            width: 400,
            marginBottom: 20,
            fontSize: 17,
          }}
        >
          We will send SMS with code into your phone
          <br /> to verify your registration
        </div>

        {selectedAlert && (
          <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
            {selectedAlert.text}
          </div>
        )}
        {isError && (
          <div
            style={{
              width: 400,
              margin: "auto",
              textAlign: "center",
              color: "red",
              marginBottom: 10,
            }}
          >
            {errMessage}
          </div>
        )}

        <div>
          {selectedSection === "enter-code" && allowEnterCode && (
            <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                  <div style={{ overflow: "hidden" }}>
                    <span className={styles["phone-prefix"]}>+7</span>
                    <input
                      type="text"
                      value={selectedPhone}
                      onChange={(event) => {
                        setSelectedPhone(event.currentTarget.value);
                        setUseUpdatePhone(true);
                      }}
                      placeholder="(___) ___-__-__"
                    />
                  </div>
                </label>
              </div>

              {phoneUpdated && (
                <div>
                  <div className={styles["code"]}>
                    <label>
                      {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                      <input
                        type="text"
                        value={selectedCode}
                        onChange={(event) => {
                          setSelectedCode(event.currentTarget.value);
                        }}
                        placeholder="Code"
                      />
                    </label>
                  </div>
                  <div className={styles["update-phone-container"]}>
                    <button
                      className={styles["update-phone-btn"]}
                      onClick={handleConfirm}
                    >
                      Confirm update phone
                    </button>
                  </div>
                </div>
              )}
            </form>
          )}

          <div>
            {!allowRequestNewConfirmationCode &&
              waitIntervalBetweenTries > 0 &&
              selectedSection === "enter-code" &&
              phoneUpdated && (
                <div
                  style={{
                    width: 400,
                    fontSize: 17,
                    textAlign: "center",
                    margin: "auto",
                  }}
                >
                  Please wait {waitIntervalBetweenTries / 1000} sec before
                  requesting <br />
                  new confirmation code
                </div>
              )}

            {allowRequestNewConfirmationCode &&
              selectedSection === "enter-code" && (
                <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
                  <div>
                    {!allowEnterCode && (
                      <div className={styles["code"]}>
                        <label>
                          {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                          <input
                            type="text"
                            value={selectedPhone}
                            disabled={true}
                            placeholder="Phone"
                          />
                        </label>
                      </div>
                    )}

                    {phoneUpdated && (
                      <div>
                        <button
                          className={styles["request-confirmation-code-btn"]}
                          onClick={handleRequestNewConfirmationCode}
                        >
                          Request new confirmation code
                        </button>
                      </div>
                    )}
                  </div>
                </form>
              )}
          </div>
        </div>
      </div>
      <br />
      <br />
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(ChangePhonePage)));
