import React from "react";
import { useState } from "react";
import VKButton from "components/VKButton";
import LayoutDefault from "components/LayoutDefault";
import debounce from "lodash.debounce";
import Inputmask from "inputmask";

import styles from "./Registration.module.scss";
import UserStore from "../../stores/UserStore";
import { RouteComponentProps } from "react-router";
import { inject } from "mobx-react";
interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const Registration: React.FC<IProps> = (props) => {
  // POST /auth/register

  const { state, user } = props.UserStore;

  const [selectedPhone, setSelectedPhone] = useState("");
  const [selectedPassword, setSelectedPassword] = useState("");
  const [selectedFirstName, setSelectedFirstName] = useState("");
  const [selectedLastName, setSelectedLastName] = useState("");

  React.useEffect(() => {
    const existingSelectedPhone = sessionStorage.getItem("selectedPhone");
    if (existingSelectedPhone) {
      sessionStorage.removeItem("selectedPhone");
      setSelectedPhone(existingSelectedPhone);
    }
    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
  }, [selectedPhone]);

  const canRegister = () => {
    return (
      selectedFirstName.trim() &&
      selectedLastName.trim() &&
      selectedPhone.trim() &&
      selectedPassword.trim()
    );
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleRegister();
  };

  const handleRegister = debounce(() => {
    if (canRegister()) {
      UserStore.register(
        selectedFirstName.trim(),
        selectedLastName.trim(),
        selectedPhone,
        selectedPassword
      ).then((res) => {
        sessionStorage.setItem("token", res["token"]);
        // redirect
        props.history.push("/confirm");
      });
    }
  }, 300);

  return (
    <LayoutDefault>
      <div className={styles["registration-page"]}>
        <div className={styles["registration-form"]}>
          <div>
            <h2>Registration</h2>
            <form onSubmit={handleSubmit}>
              <div className={styles["first-name"]}>
                <label>
                  <input
                    type="text"
                    value={selectedFirstName}
                    onChange={(event) => {
                      setSelectedFirstName(event.currentTarget.value);
                    }}
                    placeholder="First Name"
                  />
                </label>
              </div>
              <div className={styles["last-name"]}>
                <label>
                  <input
                    type="text"
                    value={selectedLastName}
                    onChange={(event) => {
                      setSelectedLastName(event.currentTarget.value);
                    }}
                    placeholder="Last Name"
                  />
                </label>
              </div>
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  <div style={{ overflow: "hidden" }}>
                    <span className={styles["phone-prefix"]}>+7</span>
                    <input
                      type="text"
                      value={selectedPhone}
                      onChange={(event) => {
                        setSelectedPhone(event.currentTarget.value);
                      }}
                      placeholder="(___) ___-__-__"
                    />
                  </div>
                </label>
              </div>
              <div className={styles["password"]}>
                <label>
                  <input
                    type="password"
                    value={selectedPassword}
                    onChange={(event) => {
                      setSelectedPassword(event.currentTarget.value);
                    }}
                    placeholder="Password"
                  />
                </label>
              </div>
              <button
                className={styles["registration-btn"]}
                disabled={!canRegister}
                onClick={handleRegister}
              >
                Register
              </button>
            </form>

            <VKButton />

            <div style={{ textAlign: "center", marginBottom: 10 }}>
              <span>
                Already have an account? Use{" "}
                <a
                  href="/signin"
                  style={{ color: "initial", cursor: "pointer", fontSize: 17 }}
                >
                  sign in
                </a>
              </span>
            </div>
          </div>
        </div>
      </div>
    </LayoutDefault>
  );
};

export default inject("UserStore")(Registration);
