import React from "react";
import { useState } from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import Inputmask from "inputmask";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";
import debounce from 'lodash.debounce';
import styles from "./UserPage.module.scss";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPaperclip,
  faPaperPlane,
  faBars,
} from "@fortawesome/free-solid-svg-icons";

import {
  // BrowserRouter as Router,
  // Switch,
  // Route,
  useParams,
} from "react-router-dom";
import {stringify} from "querystring";
// import Icon from '@material-ui/core/Icon';
import { useImmer } from 'use-immer';
import WebsocketService from "../../stores/WebsocketService";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const Dialog: React.FC<IProps> = (props) => {
  // GET /users/profile
  // POST /users/

  // eslint-disable-next-line no-console
  const { state, user } = props.UserStore;

  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");

  const [selectedAlert, setSelectedAlert] = useState("" as any);

  const [archivedDialogs, setArchivedDialogs] = useState("" as any);
  const [latestDialogs, setLatestDialogs] = useState("" as any);

  const [dialogMessages, setDialogMessages] = useState([]);
  const [dialogReceiver, setDialogReceiver] = useState("" as any);

  const [useUpdateDialogReceiver, setUseUpdateDialogReceiver] = useState(false);


  WebsocketService.startWebsocketConnection();

  const { receiverId } = useParams();
  // const [ receiverId, setReceiverId ] = useState(_receiverId || "");

  function loadArchivedDialogs() {
    return new Promise((resolve, reject) => {
      return resolve({
        entities: [
          {
            receiver: {
              user_id: 49,
              avatar_url:
                "http://localhost:8888/avatars/f93910b68a6a08d8848bae44ff554aa87.jpg",
              first_name: "",
              last_name: "",
            },
          },
        ],
        pagination: {
          page: 1,
          per_page: 1,
          total_items: 1,
        },
      });
    });
  }
  function loadLatestDialogs() {
    return new Promise((resolve, reject) => {
      return resolve({
        entities: [
          {
            receiver: {
              user_id: 48,
              avatar_url:
                "http://localhost:8888/avatars/544626bada542891434f3c317a713a79.jpg",
              first_name: "Alexandra",
              last_name: "Parasuhina",
            },
          },
          {
            receiver: {
              user_id: 47,
              avatar_url:
                "http://localhost:8888/avatars/237521ad219dcdda3bcf64c0b9d0210aa.jpg",
              first_name: "Apollinaria",
              last_name: "Sergeeva",
            },
          },
        ],
        pagination: {
          page: 1,
          per_page: 2,
          total_items: 2,
        },
      });
    });
  }
  function loadDialogMessages() {
    return new Promise((resolve, reject) => {
      return resolve([]);
    });
  }
  function loadDialogReceiver() {
    // return new Promise((resolve, reject) => {
    //   return resolve({
    //     user_id: 47,
    //     first_name: "Apollinaria",
    //     last_name: "Sergeeva",
    //     phone: "+7 906 249 88 78",
    //     avatar_url:
    //       "http://localhost:8888/avatars/237521ad219dcdda3bcf64c0b9d0210aa.jpg",
    //
    //     photos: 0,
    //     videos: 0,
    //     files: 1,
    //     shared_links: 1,
    //     voice_messages: 0,
    //   });
    // });
  }

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    if (!archivedDialogs) {
      loadArchivedDialogs().then((res) => {
        setArchivedDialogs(res);
      });
    }
    if (!latestDialogs) {
      UserStore.getProfile().then((res) => {
        UserStore.getLatestDialogs(res.id).then((res)=>{
          setLatestDialogs(res);
        })
      });
    }
    if (!dialogMessages) {
      // loadDialogMessages().then((res) => {
      //   setDialogMessages([]);
      // });
    }
    if (!dialogReceiver) {
      UserStore.getProfileById(receiverId).then((res) => {
        setDialogReceiver(res);
      });
    }
    if (!user) {
      UserStore.getProfile().then((res) => {
      });
    }
    if(useUpdateDialogReceiver) {
      UserStore.getProfileById(receiverId).then((res) => {
        setDialogReceiver(res);
      });
    }
    if(useUpdateDialogReceiver) {
      handleOnConnected();
    }
  }, [receiverId]);

  const handleOpenDialog = (event) => {
    const userId = event.currentTarget.dataset['userId'];
    if(userId === receiverId) {
      // keep
      return;
    }

    // setReceiverId(userId);
    setDialogMessages([]);
    setUseUpdateDialogReceiver(true);
    return props.history.push(`/dialog/${userId}`);
  };


  WebsocketService.registerMessageCallback(handleOnMessage);
  WebsocketService.registerOnConnectedCallback(handleOnConnected);

  function handleOnMessage(message) {
    // on connection to server -> receive history
    let json;
    try {
      json = JSON.parse(message.data);
    } catch (e) {
      // eslint-disable-next-line no-console
      console.log("This doesn't look like a valid JSON: ", message.data);
      return;
    }
    if (json.type === "history") {
      setDialogMessages(json.data);
    } else if (json.type === "message") {
      setDialogMessages(dialogMessages.concat([json.data]));
    } else {
      // eslint-disable-next-line no-console
      console.log("Hmm..., I've never seen JSON like this: ", json);
    }

    // eslint-disable-next-line no-console
    console.log(message);
  }

  function handleOnConnected(){
    return UserStore.getProfile().then((res)=>{
      // keep
      WebsocketService.send(JSON.stringify({ event: "connected", data: {sender: res, receiver_id: receiverId} }));
    });
  }

  // handleOnChangeMessage -> send draftMessage

  const sendMessage = (msg) => {
    WebsocketService.send(
        JSON.stringify({
          event: "message",
          data: { msg: msg, from: user, to: receiverId },
        })
    );
  };
  const sendDraftMessage = (msg) => {
    return new Promise((resolve,reject)=>{
      WebsocketService.send(
        JSON.stringify({
          event: "draft-message",
          data: { msg: msg, from: user, to: receiverId },
        })
      );
      resolve();
    });
  };

  let timerId;
  function handleOnChangeMessage(event) {
    const msg = event.currentTarget.value;
    if (!msg) {
      return;
    }
    if(timerId) {
      clearTimeout(timerId);
    }
    timerId = setTimeout(()=>{
      sendDraftMessage(msg).then(()=>{
        UserStore.getLatestDialogs(user.id).then((res)=>{
          setLatestDialogs(res);
        })
      });
    }, 300);
  }

  const handleKeydown = (event) => {
    if (event.keyCode === 13) {
      const msg = event.currentTarget.value;
      if (!msg) {
        return;
      }
      event.currentTarget.value = "";
      sendMessage(msg);
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    const msg = (document.getElementById('dialog-messages-new-message') as any).value;
    if (!msg) {
      return;
    }
    sendDraftMessage(msg);
  };

  return (
    <LayoutUser>

      {user && latestDialogs && dialogMessages && dialogReceiver && (
          <div className={styles["chat-page"]}>

            <div className={styles["chats-page-top-headbar"]} />
            <div className={styles["chat-page-content"]}>
              <div className={styles["chats"]}>
                <div className={styles["chats-actions"]}>
                  <div className={styles["chats-more-container"]}>
                    <a href="#" className={styles["chats-more-btn"]}>
                      <FontAwesomeIcon icon={faBars} />
                    </a>
                  </div>
                  <div className={styles["chats-search-container"]}>
                    <input
                      className={styles["chats-search"]}
                      type="text"
                      placeholder="Search"
                    />
                  </div>
                </div>
                <div className={styles["chats-latest"]}>
                  {latestDialogs.map((item) => {
                    return (
                      <a
                        href="#"
                        onClick={handleOpenDialog}
                        data-user-id={item["receiver"]["id"]}
                        className={`${
                          styles["chats-latest-item"]
                        } chats-latest-item ${
                          Number(dialogReceiver.profile["id"]) ===
                          Number(item["receiver"]["id"])
                            ? styles["active"]
                            : ""
                        }`}
                      >
                        <img src={item["receiver"].avatar_url} />{" "}
                        <span>
                          {item["receiver"].first_name}{" "}
                          {item["receiver"].last_name}
                        </span>
                      </a>
                    );
                  })}
                </div>
              </div>
              <div className={styles["dialog"]}>
                <div className={styles["dialog-receiver"]}>
                  <div className={styles["dialog-receiver-name"]}>
                    {dialogReceiver.profile.first_name} {dialogReceiver.profile.last_name}
                  </div>
                  <div className={styles["dialog-receiver-last-seen"]}>
                    last seen 7 minutes ago
                  </div>
                </div>
                <div className={styles["dialog-messages"]}>
                  {dialogMessages.map((item) => {
                    if (item.entity_type === "message") {
                      return (
                        <div
                          className={`${styles["dialog-messages-message"]} ${
                            item["authorId"] === user.id
                              ? styles["from-user"]
                              : styles["from-receiver"]
                          }`}
                        >
                          {item.message_content}
                          {/*<span className={styles['dialog-messages-message-datetime']}>{ [*/}
                          {/*    [new Date(item.created).getFullYear(), new Date(item.created).getMonth() + 1 > 12 ? 1 : new Date(item.created).getMonth() + 1, new Date(item.created).getDate()].join('-'),*/}
                          {/*    [new Date(item.created).getHours(), new Date(item.created).getMinutes(), new Date(item.created).getSeconds()].join(':')*/}
                          {/*].join(' ') }</span>*/}
                        </div>
                      );
                    }
                  })}
                </div>
                <div
                  className={styles["dialog-messages-new-message-container"]}
                >
                  <a
                    href="#"
                    className={styles["dialog-messages-add-attachment-btn"]}
                  >
                    <FontAwesomeIcon icon={faPaperclip} />
                  </a>
                  <input
                    id="dialog-messages-new-message"
                    className={styles["dialog-messages-new-message"]}
                    type="text"
                    onKeyDown={handleKeydown}
                    onChange={handleOnChangeMessage}
                    placeholder="Write a message"
                  />
                  <a href="#" onClick={handleSubmit} className={styles["dialog-messages-submit-btn"]}>
                    <FontAwesomeIcon icon={faPaperPlane} />
                  </a>
                </div>
              </div>
              <div className={styles["receiver"]}>
                <div className={styles["receiver-content"]}>
                  <span>User Info</span>
                  <div className={styles["receiver-person"]}>
                    <img
                      className={styles["receiver-person-avatar"]}
                      src={dialogReceiver.profile.avatar_url}
                    />
                    <div>
                      <div className={styles["receiver-person-name"]}>
                        {dialogReceiver.first_name} {dialogReceiver.profile.last_name}
                      </div>
                      <div className={styles["receiver-person-last-seen"]}>
                        last seen 7 minutes ago
                      </div>
                    </div>
                  </div>
                </div>
                <div className={styles["receiver-divider"]}/>
                <div className={styles["receiver-phone"]} style={{paddingBottom: 20}}>
                  <div className={styles["receiver-phone-value"]}>
                    {dialogReceiver.profile.phone}
                  </div>
                  <div className={styles["receiver-phone-label"]}>Mobile</div>
                </div>
                <div className={styles["receiver-divider"]}/>
                <div className={styles["receiver-photos"]}>
                  <div className={styles["receiver-photos-value"]}>
                    {dialogReceiver.total_photos}
                  </div>
                  <div className={styles["receiver-photos-label"]}>photos</div>
                </div>

                <div className={styles["receiver-files"]}>
                  <div className={styles["receiver-files-value"]}>
                    {dialogReceiver.total_files}
                  </div>
                  <div className={styles["receiver-files-label"]}>files</div>
                </div>

                <div className={styles["receiver-shared-links"]}>
                  <div className={styles["receiver-shared-links-value"]}>
                    {dialogReceiver.total_shared_links}
                  </div>
                  <div className={styles["receiver-shared-links-label"]}>shared links</div>
                </div>
              </div>
            </div>
          </div>
        )}
    </LayoutUser>
  );
};

export default inject("WebsocketService")(inject("UserStore")(withRouter(observer(Dialog))));
