  controlPoints: ControlPoint[] = null;
  controlPointPagination: Pagination = null;

  profile: any = null;

  loginVk = (code: string) => {
    this.state = RequestState.LOADING;

    return fetch(`${process.env.REACT_APP_API_HOST}/auth/login/vk`, {
      method: "POST",
      body: JSON.stringify({ code }),
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((res) => {
        switch (res.status) {
          case 200:
          case 201:
            return res.json();
          default:
            return res.json().then((errBody) => {
              this.setError();
              return Promise.reject({
                message: errBody.message,
              });
            });
        }
      })
      .then((user) => this.setUser(user));
  };

  getProfileById = (user_id) => {
    this.state = RequestState.LOADING;
    const token = sessionStorage.getItem("token");

    if (!token) {
      return Promise.reject();
    }

    return fetch(`${process.env.REACT_APP_API_HOST}/users/${user_id}`, {
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
    })
        .then((res) => {
          switch (res.status) {
            case 200:
            case 201:
              return res.json();
            default:
              this.setError();
              return Promise.reject();
          }
        });
  };

