import React from "react";
import { useState } from "react";
import VKButton from "components/VKButton";
import LayoutDefault from "components/LayoutDefault";
import Inputmask from "inputmask";

import styles from "./SignInPage.module.scss";
import UserStore from "../../stores/UserStore";
import { RouteComponentProps } from "react-router";
interface IProps extends RouteComponentProps {}

const SignInPage: React.FC<IProps> = (props) => {
  // POST /auth/login
  // POST /auth/login/vk

  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");

  const [selectedPhone, setSelectedPhone] = useState("");
  const [selectedPassword, setSelectedPassword] = useState("");

  const [selectedAlert, setSelectedAlert] = useState("" as any);

  React.useEffect(() => {
    // sessionStorage
    if (!selectedAlert) {
      const alert = sessionStorage.getItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
  }, [selectedAlert]);

  React.useEffect(() => {
    const existingSelectedPhone = sessionStorage.getItem("selectedPhone");
    if (existingSelectedPhone) {
      sessionStorage.removeItem("selectedPhone");
      setSelectedPhone(existingSelectedPhone);
    }
    // react-input-mask
    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
  }, [selectedPhone]);

  const canChangePhone = () => {
    return (
      ["+7", selectedPhone].join("").replace(/[()-]|_| /gi, "").length === 12
    );
  };
  const canSignIn = () => {
    return canChangePhone() && selectedPassword.trim();
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleSignIn();
  };

  const handleSignIn = () => {
    if (canSignIn()) {
      setIsError(false);
      setErrMessage("");

      UserStore.signIn(selectedPhone.trim(), selectedPassword.trim())
        .then((res) => {
          // redirect
          sessionStorage.removeItem("alert");
          props.history.push("/user");
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  return (
    <LayoutDefault>
      <div className={styles["signin-page"]}>
        <div
          className={`${isError ? styles["has-error"] : ""} ${
            styles["signin-form"]
          }`}
        >
          <div>
            <h2>Sign In</h2>
            {selectedAlert && (
              <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
                {selectedAlert.text}
              </div>
            )}
            {isError && (
              <div className={`${styles["alert"]} ${styles["alert-error"]}`}>
                {errMessage}
              </div>
            )}
            <form onSubmit={handleSubmit}>
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                  <div style={{ overflow: "hidden" }}>
                    <span className={styles["phone-prefix"]}>+7</span>
                    <input
                      type="text"
                      value={selectedPhone}
                      onChange={(event) => {
                        setSelectedPhone(event.currentTarget.value);
                      }}
                      placeholder="(___) ___-__-__"
                    />
                  </div>
                </label>
              </div>
              <div className={styles["password"]}>
                <label>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Password</span>*/}
                  <input
                    type="password"
                    value={selectedPassword}
                    onChange={(event) => {
                      setSelectedPassword(event.currentTarget.value);
                    }}
                    placeholder="Password"
                  />
                </label>
              </div>
              <div className={styles["signin-container"]}>
                <button
                  className={styles["signin-btn"]}
                  disabled={!canSignIn}
                  onClick={handleSignIn}
                >
                  Sign In
                </button>
              </div>
            </form>

            <VKButton /*buttonText="Register with VK"*/ />
            {/*
                  // registration
                  // request-confirmation-code
                  // confirm-account
                */}

            <div style={{ textAlign: "center", marginBottom: 10 }}>
              <span>
                Forgot password? You may{" "}
                <a
                  href="/reset-password"
                  style={{ color: "initial", fontSize: 17 }}
                >
                  reset password
                </a>
                <br />
              </span>
              <span>
                Don't have an account? Use{" "}
                <a
                  href="/registration"
                  style={{ color: "initial", cursor: "pointer", fontSize: 17 }}
                >
                  registration
                </a>
              </span>
            </div>
          </div>
        </div>
      </div>
    </LayoutDefault>
  );
};

export default SignInPage;
