import React from "react";
import { useState } from "react";
import Inputmask from "inputmask";

import LayoutDefault from "components/LayoutDefault";

import styles from "./Onboarding.module.scss";
import UserStore from "../../stores/UserStore";
import { RouteComponentProps } from "react-router";
interface IProps extends RouteComponentProps {}

const OnboardingPage: React.FC<IProps> = (props) => {
  // POST /auth/welcome

  const [selectedPhone, setSelectedPhone] = useState("");

  const [selectedAlert, setSelectedAlert] = useState("" as any);

  React.useEffect(() => {
    if (!selectedAlert) {
      const alert = sessionStorage.getItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
    sessionStorage.removeItem("phone");
  }, [selectedAlert]);

  React.useEffect(() => {
    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
  }, []);

  const canContinue = () => {
    return selectedPhone.trim();
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleContinue();
  };

  const handleContinue = () => {
    if (canContinue()) {
      UserStore.welcome(selectedPhone.trim()).then((res) => {
        // redirect
        sessionStorage.setItem("selectedPhone", selectedPhone);

        sessionStorage.removeItem("alert");

        const phone = sessionStorage.getItem("phone");
        if (phone) {
          props.history.push("/signin");
        } else {
          props.history.push("/registration");
        }
      });
    }
  };

  return (
    <LayoutDefault>
      <div className={styles["signin-page"]}>
        <div className={styles["signin-form"]}>
          <div>
            <h2>Enter your phone</h2>
            {selectedAlert && (
              <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
                {selectedAlert.text}
              </div>
            )}
            <form onSubmit={handleSubmit}>
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  <div style={{ overflow: "hidden" }}>
                    <span className={styles["phone-prefix"]}>+7</span>
                    <input
                      type="text"
                      value={selectedPhone}
                      onChange={(event) => {
                        setSelectedPhone(event.currentTarget.value);
                      }}
                      placeholder="(___) ___-__-__"
                    />
                  </div>
                </label>
              </div>
              <button
                className={styles["signin-btn"]}
                disabled={!canContinue}
                onClick={handleContinue}
              >
                Continue
              </button>
            </form>
          </div>
        </div>
      </div>
    </LayoutDefault>
  );
};

export default OnboardingPage;
