import React from "react";
import { useState } from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import Inputmask from "inputmask";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";

import styles from "./UserPage.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const UserPage: React.FC<IProps> = (props) => {
  // GET /users/profile
  // POST /users/

  const { state, user } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");

  const [selectedAlert, setSelectedAlert] = useState("" as any);
  const [useUpdateUser, setUseUpdateUser] = useState(false);

  const [selectedFirstName, setSelectedFirstName] = useState("");
  const [selectedLastName, setSelectedLastName] = useState("");

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    if (sessionStorage.getItem("alert")) {
      const alert = sessionStorage.getItem("alert");
      sessionStorage.removeItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
    if (!user) {
      UserStore.getProfile().then((res: any) => {
        setSelectedFirstName(res.first_name);
        setSelectedLastName(res.last_name);
      });
    }
    if (!useUpdateUser) {
      const hasUseUpdateUser = sessionStorage.getItem("useUpdateUser");
      if (hasUseUpdateUser) {
        sessionStorage.removeItem("hasUseUpdateUser");
        setUseUpdateUser(true);
      }
    }
    if (useUpdateUser) {
      // setUseUpdateUser(false);
      UserStore.getProfile().then((res: any) => {
        setSelectedFirstName(res.first_name);
        setSelectedLastName(res.last_name);
      });
    }

    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }
  }, [useUpdateUser]);

  const handleLogout = () => {
    UserStore.logout();
    props.history.push("/signin");
  };

  // if (state === RequestState.ERROR) return <p>Erorr</p>;
  //
  // if (state !== RequestState.SUCCESS) {
  //   return (
  //     <LayoutUser>
  //       <p>Loading...</p>
  //     </LayoutUser>
  //   );
  // }

  const handleClick = (e) => {
    e.preventDefault();
    const uploadBtn: any = document.querySelector(".file");
    uploadBtn.focus();
    uploadBtn.click();
  };

  let file;
  const onFileChange = (fileChangeEvent) => {
    file = fileChangeEvent.target.files[0];

    const formData = new FormData();
    formData.append("file", file, file.name);

    const userId = user.id;

    UserStore.uploadImage(userId, formData).then(() => {
      return UserStore.getProfile();
    });
  };

  const canUpdateProfile = () => {
    return selectedFirstName.trim() && selectedLastName.trim();
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleUpdateProfile();
  };

  const handleUpdateProfile = () => {
    if (canUpdateProfile()) {
      setIsError(false);
      setErrMessage("");

      UserStore.updateProfile(
        user.id,
        selectedFirstName.trim(),
        selectedLastName.trim()
      )
        .then((/* res */) => {
          // redirect
          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Profile successfully updated!",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          // props.history.push("/user");
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  return (
    <LayoutUser>
      {user && (
        <div className={styles["user-page"]}>
          {(user && user.avatar_url && (
            <img
              onClick={handleClick}
              src={
                user.avatar_url.indexOf("/avatars/") > 0
                  ? [
                      process.env.REACT_APP_API_HOST,
                      "/avatars/",
                      user.avatar_url.split("/avatars/")[1],
                    ].join("")
                  : user.avatar_url
              }
              alt="vk avatar"
              className={styles["user-page__avatar"]}
            />
          )) || (
            <img
              onClick={handleClick}
              src="/default-avatar.png"
              alt="vk avatar"
              className={styles["user-page__avatar"]}
            />
          )}
          {selectedAlert && (
            <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
              {selectedAlert.text}
            </div>
          )}
          {isError && (
            <div className={`${styles["alert"]} ${styles["alert-error"]}`}>
              {errMessage}
            </div>
          )}

          <form onSubmit={handleSubmit}>
            {user && (
              <div className={styles["first-name"]}>
                <label>
                  <span>First Name</span>
                  <input
                    type="text"
                    placeholder="First Name"
                    value={selectedFirstName}
                    onChange={(event) => {
                      setSelectedFirstName(event.currentTarget.value);
                    }}
                  />
                </label>
              </div>
            )}

            {user && (
              <div className={styles["last-name"]}>
                <label>
                  <span>Last Name</span>
                  <input
                    type="text"
                    placeholder="Last Name"
                    value={selectedLastName}
                    onChange={(event) => {
                      setSelectedLastName(event.currentTarget.value);
                    }}
                  />
                </label>
              </div>
            )}

            <div className={styles["password"]}>
              <label>
                <span>Password</span>
                <input
                  type="text"
                  placeholder="Password"
                  disabled={true}
                  value="Password hidden"
                />
                <a href="/change-password">Change password</a>
              </label>
            </div>

            {user && (
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  <span>Phone</span>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                  <span className={styles["phone-prefix"]}>+7</span>
                  <input
                    type="text"
                    value={user.phone && user.phone.replace("+7", "")}
                    disabled={true}
                    placeholder={
                      (user.phone && user.phone.replace("+7", "")) ||
                      "Phone not attached"
                    }
                  />
                  <a href="/change-phone">Change phone</a>
                </label>
              </div>
            )}
            {user && (
              <div className={styles["email"]}>
                <label>
                  <span>Email</span>
                  <input
                    type="text"
                    placeholder="Email"
                    disabled={true}
                    value={user.email || "Email not attached"}
                  />
                  <a href="/change-email">Change email</a>
                </label>
              </div>
            )}
          </form>

          <div className={styles["update-profile-container"]}>
            <button
              className={styles["update-profile-btn"]}
              style={{ padding: 10 }}
              onClick={handleUpdateProfile}
            >
               
            </button>
          </div>

          <button
            onClick={handleLogout}
            className={styles["user-page__logout"]}
          >
            {" "}
            {" "}
          </button>
          <br />
          <br />

          <input
            className="file"
            style={{ display: "none" }}
            type="file"
            onChange={(ev) => onFileChange(ev)}
          />
          {/*<button color="primary" onClick={submitForm}> Upload Single</button>*/}
        </div>
      )}
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(UserPage)));
