import React from "react";
import { BrowserRouter, Switch, Route } from "react-router-dom";

import NotFound from "pages/404";
import SignInPage from "pages/SignInPage";
import UserPage from "pages/UserPage";
import HomePage from "pages/HomePage";
import ListUsersPage from "pages/ListUsersPage";
import CreateClassArea from "pages/CreateClassArea";
import CreateClassPage from "pages/CreateClassPage";
import ScheduleControlPointsPerSchoolClassPage from "pages/ScheduleControlPointsPerSchoolClassPage";
import Registration from "pages/Registration";
import ConfirmPage from "../pages/ConfirmPage";
import OnboardingPage from "../pages/Onboarding";
import ChangePasswordPage from "../pages/ChangePassword";
import ChangePhonePage from "../pages/ChangePhone";
import ChangeEmailPage from "../pages/ChangeEmail";
import ResetPasswordPage from "../pages/ResetPassword";
import Dialog from "../pages/Dialog";

interface IProps {}

const App: React.FC<IProps> = () => {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/" exact component={HomePage} />
        <Route path="/signin" component={SignInPage} />
        <Route path="/user" component={UserPage} />
        <Route path="/list-users" component={ListUsersPage} />
        <Route path="/create-class-area" component={CreateClassArea} />
        <Route path="/create-class-page" component={CreateClassPage} />
        <Route
          path="/schedule-control-point-per-school-class"
          component={ScheduleControlPointsPerSchoolClassPage}
        />
        <Route path="/registration" component={Registration} />
        <Route path="/confirm" component={ConfirmPage} />
        <Route path="/onboarding" component={OnboardingPage} />
        <Route path="/change-password" component={ChangePasswordPage} />
        <Route path="/change-phone" component={ChangePhonePage} />
        <Route path="/change-email" component={ChangeEmailPage} />
        <Route path="/reset-password" component={ResetPasswordPage} />
        <Route path="/dialog/:receiverId" component={Dialog} />

        <Route component={NotFound} />
      </Switch>
    </BrowserRouter>
  );
};

export default App;
