import React from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { useState } from "react";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";

import styles from "./ChangeEmail.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ChangeEmailPage: React.FC<IProps> = (props) => {
  // POST /auth/request-change-email-with-confirmation
  // POST /auth/request-confirmation-code-for-change-email
  // POST /auth/confirm-change-email-with-verification

  const { state, user } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");
  const [selectedAlert, setSelectedAlert] = useState("" as any);

  const [selectedEmail, setSelectedEmail] = useState("");
  const [selectedCode, setSelectedCode] = useState("");

  const [
    allowRequestNewConfirmationCode,
    setAllowRequestNewConfirmationCode,
  ] = useState(false);
  const [waitIntervalBetweenTries, setWaitIntervalBetweenTries] = useState(
    2 * 1000
  );

  const [selectedSection, setSelectedSection] = useState("enter-email");
  const [allowEnterCode, setAllowEnterCode] = useState(true);

  const [useUpdateEmail, setUseUpdateEmail] = useState(false);

  const [emailUpdated, setEmailUpdated] = useState(false);

  // set email
  // enter code
  // React.useEffect(() => { useUpdatePhone && canChangePhone() && handleUpdatePhone(); }, [selectedPhone, useUpdatePhone] );

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    if (!user) {
      UserStore.getProfile().then((res: any) => {
        setSelectedEmail(res.email);
      });
    }

    let timeoutTimer;
    if (waitIntervalBetweenTries > 0) {
      timeoutTimer = setTimeout(() => {
        setWaitIntervalBetweenTries(waitIntervalBetweenTries - 1000);
      }, 1000);
    } else if (allowRequestNewConfirmationCode === false) {
      clearTimeout(timeoutTimer);
      setAllowRequestNewConfirmationCode(true);
    }

    if (sessionStorage.getItem("alert")) {
      const alert = sessionStorage.getItem("alert");
      sessionStorage.removeItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
  });

  // if (state === RequestState.ERROR) return <p>Erorr</p>;

  // if (state !== RequestState.SUCCESS) {
  //   return (
  //     <LayoutUser>
  //       <p>Loading...</p>
  //     </LayoutUser>
  //   );
  // }

  const canConfirm = () => {
    return selectedEmail.trim() && canChangeEmail();
  };

  const validateEmail = (email) => {
    const pattern = new RegExp(
      /^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i
    );

    if (!pattern.test(email)) {
      return {
        errors: { email: "Please enter valid email address." },
        isValid: false,
      };
    }
    return {
      isValid: true,
    };
  };

  const canChangeEmail = () => {
    // valid email
    const validatedEmail = validateEmail(selectedEmail);
    return validatedEmail["isValid"];
  };
  const handleSubmitUpdateEmail = (event) => {
    event.preventDefault();
    // handleUpdateEmail();
  };

  const handleUpdateEmail = () => {
    if (canChangeEmail()) {
      // keep
    } else {
      return;
    }

    return new Promise((resolve, reject) => {
      setIsError(false);
      setErrMessage("");
      // setSelectedAlert("");
      UserStore.requestChangeEmailWithConfirmation(
        user.id,
        selectedEmail.trim()
      )
        .then((res) => {
          // redirect
          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Email changed!",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          setSelectedSection("enter-code");
          setEmailUpdated(true);

          // props.history.push("/signin");
          resolve();
        })
        .catch((err) => {
          setIsError(true);
          setErrMessage(err.message || "   ");
          reject();
        });
    });
  };

  const canRequestNewConfirmationCode = () => {
    return (
      waitIntervalBetweenTries === 0 &&
      allowRequestNewConfirmationCode &&
      canChangeEmail()
    );
  };

  const handleRequestNewConfirmationCode = () => {
    // requestConfirmationCode

    if (canRequestNewConfirmationCode() && emailUpdated) {
      // keep
    } else {
      return;
    }

    setIsError(false);
    setErrMessage("");
    // setSelectedAlert("");
    UserStore.requestChangeEmailWithConfirmation(user.id, selectedEmail.trim())
      .then((res) => {
        // redirect
        sessionStorage.removeItem("alert");

        sessionStorage.setItem(
          "alert",
          JSON.stringify({
            text: "New confirmation code successfully sent!",
          })
        );
        const alert = sessionStorage.getItem("alert");
        sessionStorage.removeItem("alert");
        setSelectedAlert(JSON.parse(alert));

        setAllowEnterCode(true);
        // props.history.push("/signin");
      })
      .catch((err) => {
        setIsError(true);
        setErrMessage(
          err.message || "     "
        );
      });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleConfirm();
  };

  const handleConfirm = () => {
    if (canConfirm()) {
      setIsError(false);
      setErrMessage("");

      UserStore.confirmChangeEmailWithVerification(
        user.id,
        selectedEmail.trim(),
        selectedCode.trim()
      )
        .then((res) => {
          // redirect
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              sessionStorage.removeItem("alert");

              sessionStorage.setItem(
                "alert",
                JSON.stringify({
                  text: "Email successfully changed!",
                })
              );
              sessionStorage.setItem("useUpdateUser", JSON.stringify(true));
              resolve();
            }, 100);
          }).then(() => {
            props.history.push("/user");
          });
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  const handleOpenChangeEmailSection = () => {
    setSelectedSection("enter-email");
  };

  return (
    <LayoutUser>
      <div className={styles["user-page"]}>
        <h2>Change email</h2>
        <div
          style={{
            margin: "auto",
            textAlign: "center",
            width: 400,
            marginBottom: 20,
            fontSize: 17,
          }}
        >
          We will send mail with code into your email
          <br /> to verify your email
        </div>

        {selectedAlert && (
          <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
            {selectedAlert.text}
          </div>
        )}
        {isError && (
          <div
            style={{
              width: 400,
              margin: "auto",
              textAlign: "center",
              color: "red",
              marginBottom: 10,
            }}
          >
            {errMessage}
          </div>
        )}

        <div>
          {selectedSection === "enter-email" && (
            <form
              onSubmit={handleSubmitUpdateEmail}
              style={{ marginBottom: 20 }}
            >
              <div className={`email ${styles["email"]}`}>
                <label>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                  <div style={{ overflow: "hidden" }}>
                    <input
                      type="text"
                      value={selectedEmail}
                      onChange={(event) => {
                        setSelectedEmail(event.currentTarget.value);
                        setUseUpdateEmail(true);
                      }}
                    />
                  </div>
                </label>
              </div>

              <div className={styles["update-email-container"]}>
                <button
                  className={styles["update-email-btn"]}
                  onClick={handleUpdateEmail}
                >
                  Update email
                </button>
              </div>
            </form>
          )}

          {selectedSection === "enter-code" && allowEnterCode && (
            <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
              <div className={`email ${styles["email"]}`}>
                <input type="text" value={selectedEmail} disabled={true} />
              </div>

              {emailUpdated && (
                <div>
                  <div className={styles["code"]}>
                    <label>
                      <input
                        type="text"
                        value={selectedCode}
                        onChange={(event) => {
                          setSelectedCode(event.currentTarget.value);
                        }}
                        placeholder="Code"
                      />
                    </label>
                  </div>
                  <div className={styles["update-email-container"]}>
                    <button
                      className={styles["update-email-btn"]}
                      onClick={handleConfirm}
                    >
                      Confirm update email
                    </button>
                  </div>
                </div>
              )}
            </form>
          )}

          <div>
            {!allowRequestNewConfirmationCode &&
              waitIntervalBetweenTries > 0 &&
              selectedSection === "enter-code" && (
                <div
                  style={{
                    width: 400,
                    fontSize: 17,
                    textAlign: "center",
                    margin: "auto",
                  }}
                >
                  Please wait {waitIntervalBetweenTries / 1000} sec before
                  requesting <br />
                  new confirmation code
                </div>
              )}

            {allowRequestNewConfirmationCode &&
              selectedSection === "enter-code" && (
                <div>
                  <div>
                    <button
                      className={styles["request-confirmation-code-btn"]}
                      onClick={handleRequestNewConfirmationCode}
                    >
                      Request new confirmation code
                    </button>
                  </div>
                  <div
                    style={{
                      textAlign: "center",
                      marginBottom: 10,
                      marginTop: 20,
                    }}
                  >
                    <span>
                      Incorrect email? You may{" "}
                      <a
                        href="#"
                        style={{ color: "initial", fontSize: 17 }}
                        onClick={handleOpenChangeEmailSection}
                      >
                        change email
                      </a>
                      <br />
                    </span>
                  </div>
                </div>
              )}
          </div>
        </div>
      </div>
      <br />
      <br />
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(ChangeEmailPage)));
