import React from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { useState } from "react";
import Inputmask from "inputmask";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";
import styles from "./ResetPassword.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ResetPasswordPage: React.FC<IProps> = (props) => {
  // POST /auth/request-confirmation-code-for-reset-password-using-phone
  // POST /auth/verify-confirmation-code-using-phone
  // POST /auth/confirm-reset-password-with-confirmation-using-phone

  const { state } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");
  const [selectedAlert, setSelectedAlert] = useState("" as any);

  const [selectedPhone, setSelectedPhone] = useState("");
  const [selectedCode, setSelectedCode] = useState("");

  const [
    allowRequestNewConfirmationCode,
    setAllowRequestNewConfirmationCode,
  ] = useState(false);
  const [waitIntervalBetweenTries, setWaitIntervalBetweenTries] = useState(
    2 * 1000
  );

  const [selectedSection, setSelectedSection] = useState("enter-phone");
  const [allowEnterCode, setAllowEnterCode] = useState(true);

  // const [ useUpdateEmail, setUseUpdateEmail ] = useState(false);

  const [phoneUpdated, setPhoneUpdated] = useState(false);

  const [selectedNewPassword, setSelectedNewPassword] = useState("");
  const [selectedRepeatPassword, setSelectedRepeatPassword] = useState("");

  // set email
  // enter code
  // React.useEffect(() => { useUpdatePhone && canChangePhone() && handleUpdatePhone(); }, [selectedPhone, useUpdatePhone] );

  React.useEffect(() => {
    const phoneEl = document.querySelector(".phone input");
    if (phoneEl) {
      Inputmask({ mask: "(999) 999-99-99" }).mask(phoneEl);
    }

    let timeoutTimer;
    if (waitIntervalBetweenTries > 0) {
      timeoutTimer = setTimeout(() => {
        setWaitIntervalBetweenTries(waitIntervalBetweenTries - 1000);
      }, 1000);
    } else if (allowRequestNewConfirmationCode === false) {
      clearTimeout(timeoutTimer);
      setAllowRequestNewConfirmationCode(true);
    }

    if (sessionStorage.getItem("alert")) {
      const alert = sessionStorage.getItem("alert");
      sessionStorage.removeItem("alert");
      setSelectedAlert(JSON.parse(alert));
    }
  });

  // if (state === RequestState.ERROR) return <p>Erorr</p>;

  // if (state !== RequestState.SUCCESS) {
  //   return (
  //     <LayoutUser>
  //       <p>Loading...</p>
  //     </LayoutUser>
  //   );
  // }

  const canChangePhone = () => {
    return (
      ["+7", selectedPhone].join("").replace(/[()-]|_| /gi, "").length === 12
    );
  };
  const handleSubmitUseEmailForResetPassword = (event) => {
    event.preventDefault();
    // handleUpdateEmail();
  };
  const handleUseEmailForResetPassword = () => {
    if (canChangePhone()) {
      // keep
    } else {
      return;
    }

    return new Promise((resolve, reject) => {
      setIsError(false);
      setErrMessage("");
      // setSelectedAlert("");
      UserStore.requestConfirmationCodeForResetPasswordUsingPhone(
        selectedPhone.trim()
      )
        .then((res) => {
          // redirect
          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Phone found",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          setSelectedSection("enter-code");
          setPhoneUpdated(true);

          // props.history.push("/signin");
          resolve();
        })
        .catch((err) => {
          setIsError(true);
          setErrMessage(err.message || "   ");
          reject();
        });
    });
  };

  const canRequestNewConfirmationCode = () => {
    return (
      waitIntervalBetweenTries === 0 &&
      allowRequestNewConfirmationCode &&
      canChangePhone()
    );
  };
  const handleRequestNewConfirmationCode = () => {
    if (canRequestNewConfirmationCode() && setPhoneUpdated) {
      // keep
    } else {
      return;
    }

    setIsError(false);
    setErrMessage("");
    // setSelectedAlert("");
    UserStore.requestConfirmationCodeForResetPasswordUsingPhone(
      selectedPhone.trim()
    )
      .then((res) => {
        // redirect
        sessionStorage.removeItem("alert");

        sessionStorage.setItem(
          "alert",
          JSON.stringify({
            text: "New confirmation code successfully sent!",
          })
        );
        const alert = sessionStorage.getItem("alert");
        sessionStorage.removeItem("alert");
        setSelectedAlert(JSON.parse(alert));

        setAllowEnterCode(true);
        // props.history.push("/signin");
      })
      .catch((err) => {
        setIsError(true);
        setErrMessage(
          err.message || "     "
        );
      });
  };

  const canVerify = () => {
    return selectedPhone.trim() && canChangePhone();
  };
  const submitVerifyCode = (event) => {
    event.preventDefault();
  };
  const handleVerifyCode = () => {
    if (canVerify()) {
      setIsError(false);
      setErrMessage("");

      UserStore.verifyConfirmationCodeUsingPhone(
        selectedPhone.trim(),
        selectedCode.trim()
      )
        .then((res) => {
          // redirect

          sessionStorage.removeItem("alert");

          sessionStorage.setItem(
            "alert",
            JSON.stringify({
              text: "Code successfully verified!",
            })
          );
          const alert = sessionStorage.getItem("alert");
          sessionStorage.removeItem("alert");
          setSelectedAlert(JSON.parse(alert));

          setSelectedSection("enter-password");
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  const canResetPassword = () => {
    return (
      canChangePhone() &&
      selectedCode.trim() &&
      selectedNewPassword === selectedRepeatPassword
    );
  };
  const handleSubmitResetPassword = (event) => {
    event.preventDefault();
  };
  const handledResetPassword = () => {
    if (canResetPassword()) {
      // keep

      setIsError(false);
      setErrMessage("");

      UserStore.confirmResetPasswordWithConfirmationUsingPhone(
        selectedPhone.trim(),
        selectedCode.trim(),
        selectedNewPassword.trim()
      )
        .then((res) => {
          // redirect

          return new Promise((resolve, reject) => {
            setTimeout(() => {
              sessionStorage.removeItem("alert");

              sessionStorage.setItem(
                "alert",
                JSON.stringify({
                  text: "Password successfully changed!",
                })
              );
              sessionStorage.setItem("useUpdateUser", JSON.stringify(true));
              resolve();
            }, 50);
          }).then(() => {
            props.history.push("/user");
          });
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  return (
    <LayoutUser>
      <div className={styles["user-page"]}>
        <h2>Reset password</h2>
        <div
          style={{
            margin: "auto",
            textAlign: "center",
            width: 400,
            marginBottom: 20,
            fontSize: 17,
          }}
        >
          We will send sms with code into your phone
          <br /> to reset password
        </div>

        {selectedAlert && (
          <div className={`${styles["alert"]} ${styles["alert-success"]}`}>
            {selectedAlert.text}
          </div>
        )}
        {isError && (
          <div
            style={{
              width: 400,
              margin: "auto",
              textAlign: "center",
              color: "red",
              marginBottom: 10,
            }}
          >
            {errMessage}
          </div>
        )}

        <div>
          {selectedSection === "enter-phone" && (
            <form
              onSubmit={handleSubmitUseEmailForResetPassword}
              style={{ marginBottom: 20 }}
            >
              <div style={{ overflow: "hidden" }}>
                <div className={`phone ${styles["phone"]}`}>
                  <label>
                    {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                    <div style={{ overflow: "hidden" }}>
                      <span className={styles["phone-prefix"]}>+7</span>
                      <input
                        type="text"
                        value={selectedPhone}
                        onChange={(event) => {
                          setSelectedPhone(
                            event.currentTarget.value
                          ); /*setUseUpdatePhone(true)*/
                        }}
                        placeholder="(___) ___-__-__"
                      />
                    </div>
                  </label>
                </div>
              </div>

              <div className={styles["update-email-container"]}>
                <button
                  className={styles["update-email-btn"]}
                  onClick={handleUseEmailForResetPassword}
                >
                  Use phone
                </button>
              </div>
            </form>
          )}

          {selectedSection === "enter-code" && allowEnterCode && (
            <form onSubmit={submitVerifyCode} style={{ marginBottom: 20 }}>
              <div className={`phone ${styles["phone"]}`}>
                <label>
                  {/*<span style={{marginBottom: 5, display: 'block', fontSize: 14}}>Phone</span>*/}
                  <div style={{ overflow: "hidden" }}>
                    <span className={styles["phone-prefix"]}>+7</span>
                    <input
                      type="text"
                      value={selectedPhone}
                      onChange={(event) => {
                        setSelectedPhone(
                          event.currentTarget.value
                        ); /* setUseUpdatePhone(true) */
                      }}
                      placeholder="(___) ___-__-__"
                    />
                  </div>
                </label>
              </div>

              {phoneUpdated && (
                <div>
                  <div className={styles["code"]}>
                    <label>
                      <input
                        type="text"
                        value={selectedCode}
                        onChange={(event) => {
                          setSelectedCode(event.currentTarget.value);
                        }}
                        placeholder="Code"
                      />
                    </label>
                  </div>

                  <div className={styles["update-email-container"]}>
                    <button
                      className={styles["update-email-btn"]}
                      onClick={handleVerifyCode}
                    >
                      Verify code
                    </button>
                  </div>
                </div>
              )}
            </form>
          )}

          {selectedSection === "enter-password" && (
            <form
              onSubmit={handleSubmitResetPassword}
              style={{ marginBottom: 20 }}
            >
              <div className={styles["password"]}>
                <label>
                  <span>New Password</span>
                  <input
                    type="password"
                    placeholder="New Password"
                    value={selectedNewPassword}
                    onChange={(event) => {
                      setSelectedNewPassword(event.currentTarget.value);
                    }}
                  />
                </label>
              </div>

              <div className={styles["password"]}>
                <label>
                  <span>Repeat Password</span>
                  <input
                    type="password"
                    placeholder="Repeat Password"
                    value={selectedRepeatPassword}
                    onChange={(event) => {
                      setSelectedRepeatPassword(event.currentTarget.value);
                    }}
                  />
                </label>
              </div>

              <div className={styles["update-password-container"]}>
                <button
                  className={styles["update-password-btn"]}
                  style={{ padding: 10 }}
                  onClick={handledResetPassword}
                >
                   
                </button>
              </div>
            </form>
          )}

          {selectedSection === "enter-code" && phoneUpdated && (
            <div>
              {!allowRequestNewConfirmationCode &&
                waitIntervalBetweenTries > 0 &&
                selectedSection === "enter-code" && (
                  <div
                    style={{
                      width: 400,
                      fontSize: 17,
                      textAlign: "center",
                      margin: "auto",
                    }}
                  >
                    Please wait {waitIntervalBetweenTries / 1000} sec before
                    requesting <br />
                    new confirmation code
                  </div>
                )}

              {allowRequestNewConfirmationCode &&
                selectedSection === "enter-code" && (
                  <div>
                    <div>
                      <button
                        className={styles["request-confirmation-code-btn"]}
                        onClick={handleRequestNewConfirmationCode}
                      >
                        Request new confirmation code
                      </button>
                    </div>
                  </div>
                )}
            </div>
          )}
        </div>
      </div>
      <br />
      <br />
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(ResetPasswordPage)));
