import React from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";
import { useState } from "react";
import LayoutUser from "components/LayoutUser";

import UserStore from "stores/UserStore";

import styles from "./ChangePassword.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ChangePasswordPage: React.FC<IProps> = (props) => {
  // POST /auth/change-password

  const { state, user } = props.UserStore;
  const [isError, setIsError] = useState(false);
  const [errMessage, setErrMessage] = useState("");

  const [selectedOldPassword, setSelectedOldPassword] = useState("");
  const [selectedNewPassword, setSelectedNewPassword] = useState("");
  const [selectedRepeatPassword, setSelectedRepeatPassword] = useState("");

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    if (!user) {
      UserStore.getProfile().then((res: any) => {});
    }
  });

  // if (state === RequestState.ERROR) return <p>Erorr</p>;

  // if (state !== RequestState.SUCCESS) {
  //   return (
  //     <LayoutUser>
  //       <p>Loading...</p>
  //     </LayoutUser>
  //   );
  // }

  const canConfirm = () => {
    return (
      selectedOldPassword.trim() &&
      selectedNewPassword.trim() === selectedRepeatPassword.trim()
    );
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    handleChangePassword();
  };

  const handleChangePassword = () => {
    if (canConfirm()) {
      setIsError(false);
      setErrMessage("");
      UserStore.changePassword(
        user.id,
        selectedOldPassword.trim(),
        selectedNewPassword.trim()
      )
        .then((res) => {
          // redirect
          return new Promise((resolve, reject) => {
            setTimeout(() => {
              sessionStorage.removeItem("alert");

              sessionStorage.setItem(
                "alert",
                JSON.stringify({
                  text: "Password successfully changed!",
                })
              );
              resolve();
            }, 100);
          }).then(() => {
            props.history.push("/user");
          });
        })
        .catch((err) => {
          setErrMessage(err.message || "   ");
          setIsError(true);
        });
    }
  };

  return (
    <LayoutUser>
      <div className={styles["user-page"]}>
        <h2>Change password</h2>
        <div style={{ width: 300, fontSize: 17, margin: "auto" }}>
          Please fill out the form below <br />
          to change password <br />
          <br />
        </div>
        <form onSubmit={handleSubmit} style={{ marginBottom: 20 }}>
          {isError && (
            <div
              style={{
                width: 400,
                margin: "auto",
                textAlign: "center",
                color: "red",
                marginBottom: 10,
              }}
            >
              {errMessage}
            </div>
          )}
          <div className={styles["password"]}>
            <label>
              <span>Password</span>
              <input
                type="password"
                placeholder="Old Password"
                value={selectedOldPassword}
                onChange={(event) => {
                  setSelectedOldPassword(event.currentTarget.value);
                }}
              />
            </label>
          </div>

          <div className={styles["password"]}>
            <label>
              <span>New Password</span>
              <input
                type="password"
                placeholder="New Password"
                value={selectedNewPassword}
                onChange={(event) => {
                  setSelectedNewPassword(event.currentTarget.value);
                }}
              />
            </label>
          </div>

          <div className={styles["password"]}>
            <label>
              <span>Repeat Password</span>
              <input
                type="password"
                placeholder="Repeat Password"
                value={selectedRepeatPassword}
                onChange={(event) => {
                  setSelectedRepeatPassword(event.currentTarget.value);
                }}
              />
            </label>
          </div>

          <div className={styles["update-profile-container"]}>
            <button
              className={styles["update-profile-btn"]}
              style={{ padding: 10 }}
              onClick={(event) => {
                /*handleUpdateControlPoint()*/
              }}
            >
               
            </button>
          </div>
        </form>
      </div>
      <br />
      <br />
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(ChangePasswordPage)));
