import React from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";

import LayoutUser from "components/LayoutUser";

import { RequestState } from "types/RequestState";
import UserStore from "stores/UserStore";

import styles from "./CreateClassArea.module.scss";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const CreateClassArea: React.FC<IProps> = (props) => {
  const { state, user, users } = props.UserStore;

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // eslint-disable-next-line no-console
    console.log(token);
    if (!token) {
      return this.props.history.push("/signin");
    }
  });

  if (state === RequestState.ERROR) {
    return <p>Erorr</p>;
  }

  if (state !== RequestState.SUCCESS) {
    return (
      <LayoutUser>
        <p>Loading...</p>
      </LayoutUser>
    );
  }

  return (
    <LayoutUser>
      <h2 style={{ textAlign: "center" }}> </h2>
      <br />
      <input
        type="text"
        placeholder=""
        style={{ padding: 10, fontSize: 18, marginBottom: 20 }}
      />
      <br />

      <button style={{ padding: 10 }}></button>
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(CreateClassArea)));
