import React from "react";
import { useState } from "react";
import { observer, inject } from "mobx-react";
import { RouteComponentProps, withRouter } from "react-router-dom";

import LayoutUser from "components/LayoutUser";
// import Select from "react-select";
import { RequestState } from "types/RequestState";
import UserStore from "stores/UserStore";

import styles from "./ListUsersPage.module.scss";
import { isEmptyObj } from "../../helpers/isEmptyObj";

interface IProps extends RouteComponentProps {
  UserStore: typeof UserStore;
}

const ListUsersPage: React.FC<IProps> = (props) => {
  // GET /users
  // PUT /users

  const {
    state,
    user,
    users,
    usersPagination,
    schoolAreas,
    schoolClasses,
  } = props.UserStore;
  const [isError, setIsError] = useState(false);

  const [availableSchoolAreas, setAvailableSchoolAreas] = useState({});
  const [
    availableSchoolClassesPerUserAndSchoolArea,
    setAvailableSchoolClassesPerUserAndSchoolArea,
  ] = useState({});

  const [selectedSchoolArea, setSelectedSchoolArea] = useState({});
  const [selectedSchoolClass, setSelectedSchoolClass] = useState({});

  const [page, setPage] = useState(1);
  const [perPage, setPerPage] = useState(2);

  // handleGoToControlPointMessages

  React.useEffect(() => {
    const token = sessionStorage.getItem("token");
    // console.log(token);
    if (!token) {
      return props.history.push("/signin");
    }

    if (!user) {
      UserStore.getProfile();
    }
    if (!users) {
      UserStore.getAllUsers(page, perPage).then((res) => {
        const newSelectedSchoolArea = JSON.parse(
          JSON.stringify(selectedSchoolArea)
        );

        res.map((user) => {
          if (user.schoolArea) {
            newSelectedSchoolArea[user.id] = {
              value: user.schoolArea.id,
              label: user.schoolArea.school_area,
            };
          }
        });
        setSelectedSchoolArea(newSelectedSchoolArea);

        // schoolClass
        const newSelectedSchoolClass = JSON.parse(
          JSON.stringify(selectedSchoolClass)
        );

        res.map((user) => {
          if (user.schoolClass) {
            newSelectedSchoolClass[user.id] = {
              value: user.schoolClass.id,
              label: user.schoolClass.school_class,
            };
          }
        });
        setSelectedSchoolClass(newSelectedSchoolClass);
      });
    }
    if (users && !schoolAreas) {
      UserStore.getSchoolAreas().then((res) => {
        const newAvailableSchoolAreas = {};
        users.map((user) => {
          newAvailableSchoolAreas[user.id] = JSON.parse(
            JSON.stringify(res)
          ).map((item) => {
            return {
              value: item.id,
              label: item.school_area,
            };
          });
        });
        setAvailableSchoolAreas(newAvailableSchoolAreas);
      });
    }
    if (users && !schoolClasses) {
      UserStore.getSchoolClasses().then((res) => {
        const newAvailableSchoolClassesPerUserAndSchoolArea = {};
        users.map((user) => {
          newAvailableSchoolClassesPerUserAndSchoolArea[user.id] = JSON.parse(
            JSON.stringify(res)
          )
            .filter((schoolClass) => {
              if (selectedSchoolArea[user.id]) {
                return selectedSchoolArea[user.id].value === schoolClass.schoolArea.id;
              }
              return true;
            })
            .map((item) => {
              return {
                value: item.id,
                label: item.school_class,
              };
            });
        });
        setAvailableSchoolClassesPerUserAndSchoolArea(
          newAvailableSchoolClassesPerUserAndSchoolArea
        );
      });
    }
  }, [
    schoolAreas,
    schoolClasses,
    availableSchoolAreas,
    availableSchoolClassesPerUserAndSchoolArea,
    selectedSchoolArea,
    selectedSchoolClass,
    user,
    users,
    schoolAreas,
    schoolClasses,
  ]);

  const renderRow = (user) => {
    const handleChange1 = (sO) => {
      const newSelectedSchoolArea = JSON.parse(
        JSON.stringify(selectedSchoolArea)
      );
      newSelectedSchoolArea[user.id] = availableSchoolAreas[user.id].filter((item)=>{ return item.value === Number(sO.currentTarget.value)})[0];
      setSelectedSchoolArea(newSelectedSchoolArea);

      const newAvailableSchoolClassesPerUserAndSchoolArea = {};
      users.map((user) => {
        newAvailableSchoolClassesPerUserAndSchoolArea[user.id] = JSON.parse(
          JSON.stringify(schoolClasses)
        )
          .filter((schoolClass) => {
            if (newSelectedSchoolArea[user.id]) {
              return (
                newSelectedSchoolArea[user.id].value ===
                schoolClass.schoolArea.id
              );
            }
            return true;
          })
          .map((item) => {
            return {
              value: item.id,
              label: item.school_class,
            };
          });
      });
      setAvailableSchoolClassesPerUserAndSchoolArea(
        newAvailableSchoolClassesPerUserAndSchoolArea
      );

      const newSelectedSchoolClass = JSON.parse(
        JSON.stringify(selectedSchoolClass)
      );
      newSelectedSchoolClass[user.id] = { value: "", label: "" };
      setSelectedSchoolClass(newSelectedSchoolClass);
    };

    const handleChange2 = (sO) => {
      const newSelectedSchoolClass = JSON.parse(
        JSON.stringify(selectedSchoolClass)
      );
      newSelectedSchoolClass[user.id] = availableSchoolClassesPerUserAndSchoolArea[user.id].filter((item)=>{ return item.value === Number(sO.currentTarget.value) })[0];
      setSelectedSchoolClass(newSelectedSchoolClass);
    };

    const handleUpdate = () => {
      // user
      const userModel = {
        id: user.id,
        vk_id: user.vk_id,
        name: user.name,
        email: user.email,
        grant: user.grant,
        avatar_url: user.avatar_url,
        school_area_id: selectedSchoolArea[user.id].value,
        school_class_id: selectedSchoolClass[user.id].value,
      };

      UserStore.updateUser(userModel)
        .then((res) => {})
        .catch(() => setIsError(true));
    };

    if (availableSchoolAreas) {
      // keep
    } else {
      return;
    }
    if (availableSchoolClassesPerUserAndSchoolArea) {
      // keep
    } else {
      return;
    }
    if (selectedSchoolArea) {
      // keep
    } else {
      return;
    }
    if (setSelectedSchoolClass) {
      // keep
    } else {
      return;
    }

    return (
      <tr key={user["vk_id"]}>
        <td style={{ width: 100 }}>{user["vk_id"]}</td>
        <td style={{ width: 200, textAlign: "left" }}>
          {user.first_name} {user.last_name}
          <br />{" "}
          <a style={{ color: "#2980b9" }} href={`/dialog/${user.id}`}>
              
          </a>
        </td>
        <td>
          <div style={{ width: 100 }}>
            <img style={{ width: 50, height: 50 }} src={user["avatar_url"]} />
          </div>
        </td>
        <td style={{ width: 200, paddingRight: 10 }}>
          <select
            style={{
              width: 245,
              paddingTop: 10,
              paddingBottom: 10,
              paddingLeft: 20,
              paddingRight: 20,
              padding: 10,
              fontSize: 17,
            }}
            value={selectedSchoolArea[user.id] && selectedSchoolArea[user.id].value || ""}
            onChange={(event) => {
              handleChange1(event);
            }}
          >
            <option value="">Not selected</option>
            {availableSchoolAreas[user.id] &&
            Object.keys(availableSchoolAreas[user.id]).map((key) => {
              const item = availableSchoolAreas[user.id][key];
              return <option value={item.value}>{item.label}</option>;
            })}
          </select>
        </td>
        <td style={{ width: 200, paddingRight: 10 }}>

          <select
            style={{
              width: 245,
              paddingTop: 10,
              paddingBottom: 10,
              paddingLeft: 20,
              paddingRight: 20,
              padding: 10,
              fontSize: 17,
            }}
            value={selectedSchoolClass[user.id] && selectedSchoolClass[user.id].value || ""}
            onChange={(event) => {
              handleChange2(event)
            }}
          >
            <option value="">Not selected</option>
            { availableSchoolClassesPerUserAndSchoolArea[user.id] && Object.keys(availableSchoolClassesPerUserAndSchoolArea[user.id]).map((key) => {
              const item = availableSchoolClassesPerUserAndSchoolArea[user.id][key];
              return <option value={item.value}>{item.label}</option>;
            })}
          </select>
        </td>
        <td>
          <button onClick={handleUpdate} style={{ padding: 10 }}>
            
          </button>
        </td>
      </tr>
    );
  };

  if (state === RequestState.ERROR) {
    return <p>Erorr</p>;
  }

  if (state !== RequestState.SUCCESS) {
    return (
      <LayoutUser>
        <p>Loading...</p>
      </LayoutUser>
    );
  }

  const changePage = (event) => {
    const page = Number(event.currentTarget.text);
    setPage(page);
    UserStore.setUsers(null);
    UserStore.setUsersPagination(null);
  };
  const changePerPage = (event) => {
    const per_page = Number(event.currentTarget.value);
    setPerPage(per_page);
    UserStore.setUsers(null);
    UserStore.setUsersPagination(null);
  };

  return (
    <LayoutUser>
      <h2 style={{ textAlign: "center" }}>
             
      </h2>
      <br />

      {users && schoolAreas && schoolClasses && (
        <table className={styles["list-user"]} style={{ marginBottom: 40 }}>
          <thead style={{ textAlign: "left" }}>
            <tr>
              <th>Vk ID</th>
              <th>  </th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => {
              return renderRow(user);
            })}
          </tbody>
        </table>
      )}
      {users && (
        <div>
          <div style={{ marginBottom: 20 }}>
            <span> : </span>
            <select
              value={perPage}
              onChange={changePerPage}
              style={{ padding: 10, fontSize: 14 }}
            >
              {[2, 5, 10, 25, 50, 100].map((x, i) => {
                return <option key={x}>{x}</option>;
              })}
            </select>
          </div>
          <div>
            <span>: </span>
            {[...Array(usersPagination.total_pages)].map((x, i) => {
              if (usersPagination.page === i + 1) {
                return (
                  <span style={{ marginRight: 10, textDecoration: "initial" }}>
                    {i + 1}
                  </span>
                );
              } else {
                return (
                  <a
                    onClick={changePage}
                    href="#"
                    style={{
                      marginRight: 10,
                      textDecoration: "initial",
                      color: "#2980b9",
                      cursor: "pointer",
                    }}
                  >
                    {i + 1}
                  </a>
                );
              }
            })}
          </div>
        </div>
      )}
      {isError && <p style={{ color: "red" }}> </p>}
    </LayoutUser>
  );
};

export default inject("UserStore")(withRouter(observer(ListUsersPage)));
